import React from 'react';
import Logo from '../data/images/logo.webp';
import { Link } from "react-router-dom";
import '../App.css';
import data from '../data/data';

function Footer() {
    return (
        <>
        <hr className='mt-3 hr-style-one'  />
        <div className='footer row  footer-logo-padding'  style={{ margin: "auto", float: "none" }} data-aos="fade-down">
            
            <div className='col-lg-4 ' >
                <img className='footer-log' src={Logo} alt="logoimage" />
                <br />
                <p>
                    <b>{data.footer.quote}</b>
                </p>
                
                
            </div>
            <div className='col-lg-4' style={{ paddingTop: "30px" }}>
                <div>
                    <h4>
                        <ins>
                            Quick Links
                        </ins>
                    </h4>
                    <Link className='footer-link' to="/">
                        Home
                    </Link>
                    <br />
                    <Link className='footer-link' to="/about">
                        About Us
                    </Link>
                    <br />
                    <Link className='footer-link' to="/products">
                        Product
                    </Link>
                    <br />
                    <Link className='footer-link' to="/facility">
                        Facility
                    </Link>
                    <br />
                    <Link className='footer-link' to="/contact">
                        Contact Us
                    </Link>
                    <br/>
                    <br/>
                    <h5>
                        <ins>Visit us</ins>
                    </h5>
                    <Link className='footer-link' to="/">
                        www.sunshineengineering.co
                    </Link>
                    <br />
                    <br />
                    <span style={{ fontSize: "40px", color: "rgb(155, 35, 14)" }}>
                        <i class="fa-brands fa-twitter"></i> {''}
                        <i class="fa-brands fa-facebook"></i>{' '}
                        <i class="fa-brands fa-linkedin"></i>{' '}
                        <i class="fa-brands fa-google-plus"></i>{' '}
                    </span>
                </div>
            </div>
            <div className='col-lg-4' style={{ paddingTop: "30px" }}>
                <p>
                    {data.footer.adress}
                </p>
                <p>
                    <b>
                        <h5>
                            <ins>
                                E-Mail
                            </ins>
                        </h5>
                    </b>
                    <Link className='footer-link' to="/">
                        {data.footer.email2}
                    </Link>
                    <br />
                    <Link className='footer-link' to="/">
                        {data.footer.email1}
                    </Link>
                    <br /><br />
                    <b>
                        <h5>
                            <ins>
                                Mobile
                            </ins>
                        </h5>
                    </b>
                    {data.footer.mobile_no1}
                    <br />
                    {data.footer.mobile_no2}
                </p>
            </div>
        </div>
   </> )
}

export default Footer
