import React from 'react';
import '../App.css';
import Carousel from 'react-bootstrap/Carousel';
import ProductTile from './ProductTile';
import data from '../data/data';
import { Link } from 'react-router-dom';

function Home() {
  return (
    <>
      <Carousel>
        {
          data.media.map(item =>
            item.type === 'caraousel' &&
            <Carousel.Item>
              <img
                className="d-block w-100 carousel-heigth" 
                src=
                {item.image}
                alt="First slide"
              />
              <Carousel.Caption>
                <h1 style={{fontSize:"6vw",fontFamily:"Lucida Console"}}><i>{ item.title } </i></h1>
                <h5 style={{fontSize:"2.5vw",color:"white"}}>{ item.subtitle } </h5>
              </Carousel.Caption>
            </Carousel.Item>
            )
        }
      </Carousel>
      <div className='container mt-5'>
        <div className='row'>
        <div className='col-lg-6' data-aos="fade-down">
        <h1  style={{textAlign: "center" }}>
            <b>
              { data.home.title }
            </b>
          </h1>
        <br />
        <p className=' para-align home-descroption'>
          {data.home.description}
        </p>
            </div>
            <div className='col-lg-6' data-aos="fade-down">
            <img src={data.home.image} alt="sunshine" className='image-align-center mb-5 content-p-animation' />
            <img src={data.home.subImage} alt="homeimage" className='image-align-center mt-5 content-p-animation' style={{width:"100%",height:"400px"}}/>
          </div>
        </div>
      </div>
      <h3 className='home-quote' data-aos="fade-down"> 
        { data.home.quote }
      </h3>
      <div className='row' data-aos="fade-down" style={{margin:"auto", float:"none"}}>
              <ProductTile />
      </div>
      <div className='row container' style={{margin:"auto", float:"none"}}>
        <Link className='home-link'  to="/products">View More...</Link>
      </div>
    </>
    )
}

export default Home
